
@import url('https://fonts.googleapis.com/css2?family=Barlow:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    margin: 0px;
}

  :root{
    --red:red;
    --kala:black;
  }
  body{
    font-family: 'Barlow', sans-serif;
    background-color: var(--kala);
  }
  
  .shoeslogo{
    width: 152px;
    height: 85px;

  }
  nav{

    display: flex;
    justify-content: space-between;
    max-width: 1440px;
    margin: auto;
    height:80px;
    color: white;
    font-size: 30px;
 align-items: center;
  }

  nav ul {
    display: flex;
    list-style: none;
    gap: 74px;
  
}
.hero {
    display: flex;
    max-width: 1440px;
    margin: auto;
    align-items: center;
    height: calc(100vh - 200px);

   
}

.hero-content{
    color: white;
}

.hero .hero-content h1{
    font-size: 120px;
    font-weight: 700;
    line-height: normal;
    

}
.hero .hero-content h2{
    font-size: 55px;
    font-weight: 500;
    line-height: normal;


}
.hero .hero-content h4{
    font-size: 35px;
    font-weight: 200;
    line-height: normal;


}
.btn button{
    width: 232px;
    height: 74px;
    border-radius: 110px;
    font-size: 24px;

}



@media (max-width: 1800px) {
    nav  .logo ul{
      text-align: center;
      display:inline;
      margin: 0%;
      padding: 0%;

    }
  .hero{
    text-align: center;
    display: inline-flex;

    height: calc(100vh - 20vh);
    margin: 0;
    padding: 0%;
  }

 
  }
  
@media (max-width: 1400px) {
    nav  .logo ul{
      text-align: center;
      display:inline;
      margin: 0;
      padding: 0%;
     
   
    }
  .hero{
    text-align: center;
    display: inline-flex;
    margin: 10px 0;
    height: calc(100vh - 20vh);
    margin: 0;
    padding: 0%;
  }
  .hero .hero-content{
    margin-bottom: -200px;
  }
  
  }
   
  
  